import React from "react";
import { Button, Form as AntForm, Skeleton, message } from "antd";
import queryClient, { useMutation } from "components/utils/queryClient";
import { Form, FormContentBase, SubmitButton } from "components/ui/Form/Form";
import { AccessControlTypes, Script } from "types";
import { useParams } from "react-router-dom";
import { useQuery } from "react-query3";
import useNormalizeData from "components/utils/normalizedRecord";

export default function ScriptSettings() {
  const normalizedRecord = useNormalizeData();
  const [form] = AntForm.useForm();
  const fullPath = useParams()["*"];
  const { mutateAsync, isLoading: saving } = useMutation<Script>();
  const { data: script, isLoading } = useQuery<Script>(`/script/${fullPath}`, {
    enabled: !!fullPath,
    refetchOnMount: 'always',
    refetchOnWindowFocus: false,
  });

  async function onClick() {
    const values = await form.validateFields();
    await mutateAsync(
      {
        key: script?.resourceInfo?.parent,
        action: "update",
        ...normalizedRecord({ ...script, ...values }),
      },
      {
        onSuccess: (data, { key, action, ...rest }) => {
          message.success("Changes saved");
          queryClient.refetchQueries(`/script/path/${rest?.fullPath}`, {
            exact: true,
            stale: true,
          });
        },
      }
    );
  }

  if (isLoading) {
    return <Skeleton />
  }

  return (
    <Form schemaName="script">
      <FormContentBase
        name="script_settings_form"
        form={form}
        inEditMode={true}
        defaultValues={normalizedRecord(script)}
      />
      <SubmitButton requiredAccessControls={AccessControlTypes.Edit} requiredRoles={["Administrator", "Operator"]} accessControls={script?.accessControls}>
        <Button loading={isLoading || saving} type="primary" onClick={onClick}>
          Save Settings
        </Button>
      </SubmitButton>
    </Form>
  );
}
